import React, { useContext, useState } from "react";
import { IonModal } from "@ionic/react";
import EditDailyNoteModal from "./EditDailyNoteModal";
import { SchedulePageContext } from "./SchedulePageContext";
import { AuthorizedItemsContext } from "../../context/AuthorizedItemsContext";
import { Role } from "../../orbital-interfaces/authorized-items";

interface AddDailyNoteButtonProps {
  date: Date;
}
// --------------------------------------------------------------------------------------
//
// --------------------------------------------------------------------------------------
const AddDailyNoteButton: React.FC<AddDailyNoteButtonProps> = ({ date }) => {
  const [showEditModal, setShowEditModal] = useState(false);
  const { onForceRefresh } = useContext(SchedulePageContext);
  const { auth } = useContext(AuthorizedItemsContext);

  if (auth.role === Role.Employee) {
    return <span />;
  }

  const onClose = (toastMessage: string) => {
    setShowEditModal(false);
    onForceRefresh(toastMessage);
  };

  return (
    <>
      <button
        className="add-daily-note-button"
        onClick={() => {
          setShowEditModal(true);
        }}
      >
        Daily Note...
      </button>

      <IonModal
        isOpen={showEditModal}
        onDidDismiss={() => setShowEditModal(false)}
      >
        <EditDailyNoteModal
          isOpen={showEditModal}
          note={null}
          initialDate={date}
          onCloseModal={(toast) => {
            onClose(toast);
          }}
        />
      </IonModal>
    </>
  );
};

export default AddDailyNoteButton;
